@import url(https://fonts.googleapis.com/css?family=Roboto+Mono:100);
#app {
  height: 100%;
}

html,
body {
  font-family: 'Roboto Mono', monospace;
  background: #212121;
  height: 100%;
  margin: 0;
}
.container {
  height: 100%;
  width: 100%;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
}
.description {
  position: absolute;
  top: 20px;
  color: #fafafa;
}
.text {
  font-weight: 100;
  font-size: 28px;
  color: #fafafa;
}
.dud {
  color: #757575;
}
